import axios from 'axios';


import Vue from 'vue';
import router from '../plugins/routers'




// 发送请求是的拦截器
axios.interceptors.request.use(function(config) {
    //config 含有发出的请求的配置信息  axios(config)
    // 每次都自动携带token
    let token = window.localStorage.getItem('user')
    token = token ? JSON.parse(window.localStorage.getItem('user')) : ''
    config.headers={
    token,
    }
    // this.$store.state.Loading;
    return config;// 撒手放出经过配置的请求
  }, function(error) {
    // 发出了错误的请求，拦截
    return Promise.reject(error);
});
axios.interceptors.response.use(function(response) {
    //response  ~~ axios请求后的res
    // 响应数据回来后，到达目标组件之前，做点事   res.status   res.data.err == 2
    //校验返回数据，token过期，路由跳转login,传递当前路由地址
    let currentRoute = router.currentRoute.fullPath;//获取当前路由全路径，string
    if(response.data.err===2 && !currentRoute.includes('/login')){
      router.replace({
        path:'/login',
        query:{p:currentRoute}
      })
    }
    
    return response;//奔向组件
  }, function(error) {
    // 错误的响应，拦截
    return Promise.reject(error);
  });







// 在vue实例上绑定$axios,以便在vue中能够使用$axios 而不用在每个组件中都去引用axios
Vue.prototype.$axios=axios;
// 在window全局绑定axios
window.axios=axios;//×
// 导出axios
export default axios;