<template>
    <div id="home">
        <Loop :time='5000' :speed='2000' />
          <Captions 
        v-for="(item,index) in li" 
        :key='index'  
        :to="{name:'detail',params:{_id:item._id},query:{collectionName:'home'}}"
        :txtTitle='item.title'
        :txtCont='item.content'
         />
          
          
        


    </div>
</template>
<script>
import {mapState,mapMutations} from 'vuex';
import Loop from '../components/loop'
import Captions from '../components/captions'
export default {
    name:'home',
    data(){
      return {
        li:[],
      }
        
    },
    components:{
      Loop,Captions,
    },
  methods:{
    ...mapMutations(['changeMain','changeIcon']),
    change(){
      this.changeMain(true);
      this.changeIcon(false);
    }
  },
  mounted(){
      this.$axios({
        url:"/api/goods/home",
        method:'GET',
      }).then(res=>{
        this.li = res.data.data;
      })
  },
}
</script>
<style scoped lang='less'>
    #home{
        // height: 100%;
        padding: 50px 0;
        box-sizing: border-box;
        .title{
          width: 100%;
        }






        

    }
</style>