<template>
    <div class="loop">
        <div class="swiper-container">
            <div class="swiper-wrapper">
                <div class="swiper-slide" v-for="item of list" :key='item.id' >
                    <img   :src="item.img" alt="">
                </div> 
            </div>
        <!-- 如果需要分页器 -->
        <div class="swiper-pagination"></div>
        </div>
    </div>
</template>
<script>
import Swiper from 'swiper'
export default {
    name:'loop',
    data(){
        return {
            list:[]
        }
    },
    props:{
        time:{
            type:Number,
            required:true,
            default:1000,
        },
        speed:{
            type:Number,
            required:true,
            default:2000,
        }
    },
    methods:{
        banner(){
            this.$nextTick(()=>{
                new Swiper ('.swiper-container', {
                loop: true, // 循环模式选项
                autoplay:this.time,
                autoplayDisableOnInteraction:false,
                onlyExternal:true,
                speed:this.speed,
                // 如果需要分页器
                pagination: '.swiper-pagination',
                
                })   
            })
        }
    },
    watch:{
        
        
    },
    
    mounted(){
        
        this.$axios({
            url:'api/goods/banner',

        }).then(res=>{
            this.list = res.data.data
            
            }
            );
            this.banner();
    },
    updated(){
        this.banner();
    }

    
}
</script>
<style scoped lang='less'>
    @import url('../../node_modules/swiper/dist/css/swiper.min.css');
    .swiper-container {
    width: 100%;
    height: 200px;
    .swiper-pagination{
        width: 100%;
        height: 10px;
    }
} 
</style>