<template>
    <div id="Button">
        <button :class="c1" @click="$emit('click')">{{txt}}</button>
    </div>
</template>
<script>
export default {
    name:'Button',
    props:{
        txt:{
            type:String,
            default:'暂无数据'
        },
        c1:{
            type:String,
            default:'normal'
        }
    }
    
}
</script>
<style scoped lang='less'>
#Button{
    .normal{
            width: 250px;
            height: 40px;
            margin-top: 30px;
            outline: none;
            border: none;
            background-color: #7e9af3;
            font-size: 20px;
            color: white;
    }
}
</style>