<template>
    <div id="user">
		<div class="top">
            <div class="icon">
                <div class="icon2">
                    <img :src="$baseurl+user.icon" alt="">
                </div>
            </div>
            <div class="name">
               
                    <h2><a href="JavaScript:;">{{user.nikename}}</a></h2>
                    <p>{{user.nikename}}</p>
            </div>
        </div>
        <div class="user-main">
            <Cell txt='消息中心' c1='icon-lingdang' color="blue" fontSize='30' />
            <Cell txt='我赞过的' c1='icon-dianzan1' fontSize='30' color="#6cbc45" sum='1篇' />
            <Cell txt='收藏集' c1='icon-shoucang1' fontSize='30' color="#ffc347" sum='6个'/>
            <Cell txt='阅读过的文章' c1='icon-yuedu' fontSize='30' color="#abb3be" sum='0篇'/>
            <Cell txt='标签管理' c1='icon-yishengbiaoqianguanli' fontSize='30' color="#abb3be" sum='0个'/>
            
        </div>

        <div class="user-bottom">
            <Cell txt='意见反馈' c1='icon-yijianfankui' fontSize='30' color="#abb3be"  />
            <Cell txt='设置' c1='icon-shezhi' fontSize='30' color="#abb3be"  />
        </div>

        <div class="footer">
            <vButton @click='loginOut' txt='注销' c1='normal'/>
        </div>
        
	</div>
</template>
<script>
import {mapState,mapMutations} from 'vuex';
import Cell from '../components/cell';
import axios from '../plugins/axios';
import vButton from '../components/v-button';
export default {
    name:'user',
    beforeRouteEnter(to,from,next){
        axios({
            url:"/api/user",
            method:"GET",
        }).then(res=>{
            if(res.data.err === 0){
                // next();
                // next(to.query.user = res.data.data);
                // to.query[user] = res.data.data;
                next( _this => _this.user=res.data.data);
            }else{
                next('/login');
            }
        });
    },
    data(){
        return {
            user:{},
        }
    },
    components:{
        Cell,vButton
    },
    
    methods:{
        ...mapMutations(['changeIcon','changeMain']),
        icon(bool1,bool2){
            this.changeIcon(bool1);
            this.changeMain(bool2);
        },
        loginOut(){
            window.localStorage.removeItem('user');
            this.$router.push('/login');
        }
    },
    mounted(){
        this.icon(true,false);
        // console.log(this.$router.query.user);
       

    },
    uodated(){
        
    },
    destroyed(){
        this.icon(false,true);
    }
}
</script>
<style scoped lang="less">
@import url('../style/index.less');
#user{
    background-color: rgb(225,225,225);
    padding: 50px 0;
    box-sizing: border-box;
    
    .top{
        height: 150px;
        width: 100%;
        background-color: rgb(141, 182, 65);
        display: flex;
        .icon{
            width: 120px;
            height: 120px;
            background-color: rgba(250,250,250,.4);
            margin: 10px 0 0 10px;
            position: relative;
            border-radius: 50%;
            .icon2{
                width: 110px;
                height: 110px;
                background-color: rgba(250,250,250.5);
                position: absolute;
                margin: auto;
                left: 0;
                top: 0;
                right: 0;
                bottom: 0;
                border-radius: 50%;
                overflow: hidden;
                img{
                    display: block;
                    margin: auto;
                }
            }
        }
        .name{
            flex: 1;
            height: 100px;
            margin: 45px 0 0 20px;
            a{
                color: black;
            }
            p{
                margin-top: 10px;
            }
        }
    }
    .user-main{
        margin-top: 20px;
        i{
        font-size: 30px;
        }
    }
    .user-bottom{
        margin: 20px 0;

    }
    .footer{
        width: 100%;
        display: flex;
        align-items: color;
        justify-content: center;
        margin-bottom: 50px;
    }

}

</style>