<template>
    <div id="login">
		<div class="login-main">
            <v-Input c1='normal' type="text" v-model='uName'  pla='用户名'/>


            <!-- <input type="text" placeholder="用户名" v-model="uName"> -->
            <br>
            <!-- <input type="password" placeholder="密码" v-model="uPassowrd"> -->
            <v-Input c1='normal' type='password' v-model="uPwd" pla='密码'/>
            <br>
                {{err}}
            <br>
            <!-- <button ></button> -->
            <vButton txt="登录"  @click="add"/>
            <br>
            <router-link class="left" to="/register">没有账号?点击注册</router-link>
        </div>
	</div>
</template>
<script>
import {mapState,mapMutations} from 'vuex';
import vButton from '../components/v-button';
import vInput from '../components/v-input/v-input';
import axios from '../plugins/axios';
export default {
    name:'login',
    data(){
        return {
            uName:'',
            uPwd:'',
            err:'',
        }
    },
    components:{
        vInput,vButton
    },
    methods:{
         ...mapMutations(['changeMain','changeIcon','changeBar']),
        change(bool1,bool2,bool3){
            this.changeMain(bool1);
            this.changeIcon(bool2);
            this.changeBar(bool3);
        },
        add(){
            if(this.uName === '' || this.uPwd === ''){
                this.err = '请输入用户名和密码';
            }else{
                 this.$axios({
                   url:"/api/login",
                   method:"POST",
                   data:{
                       username:this.uName,
                       password:this.uPwd,
                   }
               }).then(res=>{
                   if(res.data.err === 0){
                       window.localStorage.setItem('user',JSON.stringify(res.data.token));
                       if(this.$route.query.p){
                           this.$router.push(this.$route.query.p);
                       }else{
                           this.$router.push('/user');
                       }
                   }else{
                       this.err = res.data.msg;
                   }

               });
            }
              
        },
        Name(v){
            this.uName = v;
        }
    },

    mounted(){
        this.change(true,false,false);
    },
    destroyed(){
        this.change(false,true,true);
    }
   
}
</script>
<style scoped lang='less'>
@import url('../style/index.less');
#login{
    height: 100%;
    padding-top: 50px;
    box-sizing: border-box;
    position: relative;
    .login-main{
        position: absolute;
        top: 15%;
        left: 50%;
        transform: translateX(-50%);
        width: 85%;

        .t_c();
        v-input{
            padding-left: 10px;
            box-sizing: border-box;
            margin-top: 30px;
            width: 250px;
            height: 30px;
            border: none;
            outline: none;
            background-color: rgb(189, 180, 180);
            font-size: 20px;
        }
        
        .left{
            float: right;
            margin-top: 10px;
            font-size: 12px;
        }
    }
}

</style>