<template>
    <div id="register">
		<div class="register-main">
            <v-Input c1='normal' type='text' v-model='uName'  pla='用户名'/>
            <br>
            <v-Input c1='normal' type='password' v-model="uPwd" pla='密码'/>
            <br>
            <v-Input c1='normal' type='text' v-model="nikeName" pla='昵称'/>
            <br>
           <input type="file" id="icon">

            <br>
            {{err}}
            <v-Button @click='reg' class="normal"  txt="注册"/>
            <br>
            <router-link class="left" to="/login">已有帐号?点击登录</router-link>
        </div>
	</div>
</template>
<script>
import {mapState,mapMutations} from 'vuex';
import vButton from '../components/v-button'
import vInput from '../components/v-input/v-input';
export default {
    name:'register',
    data(){
        return {
            uName:'',
            uPwd:'',
            nikeName:'',
            icon:'',
            err:'',
        }
    },
    components:{
        vInput,vButton
    },
    methods:{
         ...mapMutations(['changeMain','changeIcon','changeBar']),
        change(bool1,bool2,bool3){
            this.changeMain(bool1);
            this.changeIcon(bool2);
            this.changeBar(bool3);
        },
        reg(){
                let formData = new FormData();
                formData.append('username',this.uName);
                formData.append('password',this.uPwd);
                formData.append('nikename',this.nikeName);
                let icon = document.querySelector('#icon');
                formData.append('icon',icon.files[0]);

                this.$axios({
                    url:'/api/reg',
                    method:'POST',
                    data:formData,
                }).then(res=>{
                    if(res.data.err === 0){
                        console.log(res.data.data);
                        this.$router.push('/login');
                    }else{
                        this.err = res.data.msg;
                    }
                })
            }
    },

    mounted(){
        this.change(true,false,false);
    },
    destroyed(){
        this.change(false,true,true);
    }
   
}
</script>
<style scoped lang='less'>
@import url('../style/index.less');
#register{
    height: 100%;
    padding-top: 50px;
    box-sizing: border-box;
    position: relative;
    .register-main{
        position: absolute;
        top: 15%;
        left: 50%;
        transform: translateX(-50%);
        width: 85%;

        .t_c();
        input{
            padding-left: 10px;
            box-sizing: border-box;
            margin-top: 30px;
            width: 250px;
            height: 30px;
            border: none;
            outline: none;
            background-color: rgb(189, 180, 180);
            font-size: 20px;
        }
        button{
            width: 250px;
            height: 40px;
            margin-top: 30px;
            outline: none;
            border: none;
            background-color: rgb(126, 154, 243);
        }
        .left{
            float: right;
            margin-top: 10px;
            font-size: 12px;
        }
    }
}

</style>