import Vue from 'vue'
import Vuex from 'vuex'

import aHome from './homeModule'
import aUser from './userModule'
Vue.use(Vuex)



let store =  new Vuex.Store({
  state:{
        Top:true,
        Bar:true,
        Main:true,
        TopIcon:true,
        Loading:true,
  },
  modules:{   //模块化必须要有modules
    "aHome":aHome,
    "aUser":aUser,
    
  },
  mutations:{
    changeIcon(state,payload){
      state.TopIcon = payload;
    },
    changeMain(state,payload){
      state.Main = payload;
    },
    changeBar(state,payload){
      state.Bar = payload;
    }
  }
  
});

export default store;
