<template>
    <div class="Caption">
        <a @click="getDetail" href="JavaSCript:;">
            <div class="img">
                <img :src=imgSrc alt="">
            </div>
            <div class="text">
                <h2 class="title">{{txtTitle}}</h2>
                <p class="cont">{{txtCont}}</p>
            </div>
        </a>
    </div>
</template>
<script>
export default {
    name:'Caption',
    props:{
        txtTitle:{
            type:String,
            default:'数据加载中......',
        },
        txtCont:{
            type:String,
            default:'数据加载中......',
        },
        imgSrc:{
            type:String,
            default:'//bpic.588ku.com/element_origin_min_pic/19/11/20/a5a7dbafdbfdec9f9e70823578bc5467.jpg',
        },
        to:{
            type:Object,
            default:null,
        },
        url:{
            type:String,
            default:null,
        }
   },
   methods:{
       getDetail(){
           if(this.url){
               window.open(this.url,'_self');
           }else if(this.to){
               console.log(this.to.name);
               this.$router.push({name:this.to.name,params:this.to.params,query:this.to.query});
           }
       }
   }
}
</script>
<style lang="less" scoped>
    .Caption{
        width: 100%;
        height: 100px;
        background-color:rgb(230,230,230);
        margin: 5px 0;
        overflow: hidden;
        a{
            color: black;
            display: flex;
            height: 100%;
            width: 100%;
            .img{
                width: 120px;
                height: 100%;
            }
            .text{
                margin-left: 10px;
                flex: 1;
                p{
                    margin-top: 10px;
                }
            }
        }
        
    }
</style>