<template>
    <div class="vInput">
        <input
         :type="type"
         :class="`${c1}`"
         :placeholder="`${pla}`"
         :value="value"
         @change="$emit('change', $event.target.value)"
        >
        {{value}}
    </div>
</template>
<script>
export default {
    name:'vInput',
    data(){
        return {
            value:'',
        }
    },
    model: {
        prop: 'value',
        event: 'change'
    },
    props:{
        type:{
            type:String,
            default:'text',
        },
        c1:{
            type:String,
            default:'normal',
        },
        pla:{
            type:[String,Number],
            default:'',
        },
    },
    methods:{
        // change(data){
        //     this.$emit('change',data);
        //     console.log(data)
        // }
    }
}
</script>
<style lang="less" scoped>
@import url('../v-input/v-input.css');
</style>