<template>
    <div id="detail">
        <div class="main" v-if="detail.title">
            <h3><span>{{detail.title}}</span></h3>
        </div>
		
        <Loading v-else />
	</div>
</template>
<script>
import {mapState,mapMutations} from 'vuex';
import Loading from '../layouts/loading'
export default {
    name:'detail',
    data(){
        return {
            detail:{},
        }
    },
    components:{
        Loading,
    },
    methods:{
         ...mapMutations(['changeMain','changeIcon','changeBar',]),
        change(bool1,bool2,bool3){
            this.changeMain(bool1);
            this.changeIcon(bool2);
            this.changeBar(bool3);
        },
    },
    mounted(){
        this.change(false,true,false);
        let collectionName = this.$route.query.collectionName;
        let _id = this.$route.params._id;


        this.$axios({
            url:`/api/goods/${collectionName}/${_id}`,
        }).then(res=>{
            this.detail = res.data.data;

        })
    },
    destroyed(){
        this.change(true,false,true);
    }
}
</script>
<style scoped lang='less'>
    #detail{
        height: 100%;
        padding-top: 50px;
        box-sizing: border-box;
        h3{
            width: 100%;
            display: flex;
            justify-content: center;
            height: 30px;
            align-items: center;
        }
    }
</style>